<template>
  <div>
  <div class="mgm">
    <div class="mgm_header">
      <x-header :left-options="{showBack:true,backText:''}">管理收货地址</x-header>
    </div>
    <div class="mgm_content">
      <div class="address_msg" v-for="address in addressList" v-show="addressList&&addressList.length>0">
        <ul class="a_msg_1">
          <li class="a_msg_first">张三</li>
          <li	class="a_msg_last">18111661552</li>
        </ul>
        <div class="a_msg_2">
          北京市海淀区圆明园西路18号中发百旺商城3层北京市海淀区圆明园西路18号中发百旺商城3层
        </div>
        <div class="a_msg_3">
          <checker :max="1" default-item-class="default_class" selected-item-class="selected_class">
            <checker-item value="1" >
              <i class="opt_img"></i>
              <span class="opt_msg">
								默认地址
							</span>
            </checker-item>
            <div class="omit">
              <i></i>
              <em>删除</em>
            </div>
            <div class="compile">
              <i></i>
              <em @click="edit(address)">编辑</em>
            </div>
          </checker>

        </div>
      </div>
      <!--未添加地址-->
      <div class="basic_cement" v-show="!addressList&&addressList.length>0">
        <div class="bc">
          <i>
            <img src="../../assets/image/set/dizhi.png"/>
          </i>
          <p>您还没有收货地址呢！</p>
        </div>
      </div>
    </div>

    <div class="new_btn">
      <x-button type="primary" @click.native="showAddress">添加新地址</x-button>
    </div>

  </div>
    <edit-address :_address="address" ref='editAddress' v-on:child-update="child_updateAddress"></edit-address>
  </div>
</template>
<script>
  import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

  import {post} from '../../common/js/public-resource'
  import {getStorage} from '../../common/js/storage'
  import {USERINFO_STORAGE} from '../../common/public-variable'
  import editAddress from './editaddress.vue'
  export default {
    components: {
      PopupPicker,Actionsheet,XHeader,
      Cell,
      Group,
      XButton,
      Checker,
      editAddress
    },
    data () {
      return {
        color1: '#FFEF7D',
        colors1: ['#FF3B3B'],
        userInfo:{},
        addressList:[],
        address:{}
      }
    },
    methods: {
      showAddress(){
          this.address={};
        this.$refs.editAddress.show();
      },
      edit(address){
        this.address=address;
        this.$refs.editAddress.show();
      },
      child_updateAddress(address){
          this.addressList.push(address);
      }
    },
    created(){
      this.userInfo=getStorage(USERINFO_STORAGE)
      var that=this;
      post('usercenter/address/getAddressList',{token:this.userInfo.token},function (data,success) {
        if(success){
          that.addressList=data;
        }
        else{
          that.$vux.toast.show({
            text: data,
            type: 'warn',
            width: '18em'
          })
        }
      })
    }
  }
</script>

<style rel="stylesheet/less" lang="less">
  @import "../../assets/css/Management.less";
  em{
    font-style: normal;
  }
</style>



