<template>
  <transition enter-active-class="animated fadeInRight" leave-active-class="animated fadeOutRight">
  <div class="nickname" v-show="isShow" ref='nickname'>
    <div class="nickname_header">
      <x-header :left-options="{showBack:false,backText:'eee'}">
        <span class="back_btn" @click="hide"><i></i></span>
        修改昵称
      </x-header>
    </div>
    <div class="nickname_content">
      <group>
        <x-input type="text" placeholder="" v-model='userInfo.name'></x-input>
      </group>
      <button class="preserve" @click="saveNickName">保存</button>
    </div>
  </div>
  </transition>
</template>
<script type="text/ecmascript-6">
  import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

  import {post} from '../../common/js/public-resource'
  export default {
    props: {
      userInfo: {}
    },
    data(){
      return {
        isShow: false
      }
    },
    components: {
      XHeader, XInput, Group, Cell
    },
    methods: {
      show(){
        this.isShow = true;
      },
      hide(){
        this.isShow = false;
      },
      saveNickName(){
        var that = this;
        post('/usercenter/modifyInfo', {
          token: this.userInfo.token,
          name: this.userInfo.name
        }, function (data, success) {
          if (success) {
            that.$emit('child-update', that.userInfo);//调用父组件方法更新昵称
            that.$vux.toast.show({
              text: '修改成功',
              type: 'success',
              width: '18em'
            })
            that.isShow = false;
          }
          else {
            that.$vux.toast.show({
              text: data,
              type: 'warn',
              width: '18em'
            })
          }
        })
      }
    }
  }
</script>

<style rel="stylesheet/less" lang="less">
  @import "../../assets/css/nickname.less";
  .move-transition {
    transition: all 0.2s linear;
    transform: translate3d(0, 0, 0)
  }
  .move-enter, .move-leave
  {transform: translate3d(100%, 0, 0)}
</style>



