<template>
  <transition enter-active-class="animated fadeInUpBig" leave-active-class="animated fadeOutDownBig">
  <div class="new_address" v-show="isShow" ref='editAddress'>
    <div class="new_header">
      <x-header :left-options="{showBack:false,backText:''}">
        <span class="back_btn" @click="hide"><i></i></span>
        管理收货地址 <span class="save" @click="saveAddress" :class="{'color:#000':isDisable}">保存</span></x-header>
    </div>
    <div class="new_content">

      <group>
        <x-input title="收货人"  placeholder="收货人姓名"  v-model="_address.contacts"></x-input>
        <x-input title="联系电话"  placeholder="联系电话" v-model="_address.phone" keyboard="number" is-type="china-mobile" ref="phoneTxt"></x-input>
        <x-address  title="所在地区"  placeholder="请选择地址" v-model="addressVal" :list="addressData" confirm-text="确认" cancel-text="取消"></x-address>
        <x-textarea  placeholder="请填写详细地址" v-model="_address.address" :max="60" ref="addressTxt"></x-textarea>
      </group>
      <group>
        <x-switch title="设为默认" v-model="isFirst"></x-switch>
      </group>
    </div>
  </div>
  </transition>
</template>

<script>
  import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import value2name from 'vux/src/filters/value2name.js'

  import {post} from '../../common/js/public-resource'
  import {getStorage,setStorage} from '../../common/js/storage'
  import {USERINFO_STORAGE} from '../../common/public-variable'
  import {isLogin} from '../../common/js/checkIsLogin'
  export default {
    props: {
      _address: {
        contacts: '',//	//联系人
        phone: '',//联系电话
        province: '',//省
        city: '',//市
        area: '',//区
        address: '',//详细地址
        first: '',//默认 1是2否
        state: ''//状态 1正常2删除
      }
    },
    components: {
      ChinaAddressData,
      XAddress,
      XSwitch,
      XTextarea,
      XHeader,
      Cell,
      Group,
      XInput
    },
    data () {
      return {
        addressData: ChinaAddressData,
        isShow: false,
        addressVal: [],
        isFirst: false,
        isDisable: false
      }
    },
    methods: {
      show(){
        this.isShow = true;
      },
      hide(){
        this.isShow = false;
      },
      saveAddress(){
        var that = this;
        if (that._address) {
          if (!that._address.contacts) {
            that.$vux.toast.show({text: '请填写收货人姓名!', type: 'text', width: '18em'});
          }
          else if (!that._address.phone) {
            that.$vux.toast.show({text: '请填写联系电话!', type: 'text', width: '18em'});
          }
          else if (!that._address.address) {
            that.$vux.toast.show({text: '请填写详细地址!', type: 'text', width: '18em'});
          }
          else if (!that.$refs.phoneTxt.valid) {
            that.$vux.toast.show({text: '手机格式有误!', type: 'text', width: '18em'});
          }
          else if (that.addressVal.length <= 2) {
            that.$vux.toast.show({text: '请选择地址!', type: 'text', width: '18em'});
          }
          else {
            if (isLogin()) {
              var userInfo = getStorage(USERINFO_STORAGE);
              that._address.province = that.addressVal[0];
              that._address.city = that.addressVal[1];
              that._address.area = that.addressVal[2];
              that._address.first = that.isFirst ? 1 : 2;
              post('usercenter/address/addAddress', {
                token: userInfo.token,
                contacts: that._address.contacts,//	//联系人
                province: that._address.province,//省
                city: that._address.city,//市
                area: that._address.area,//区
                address: that._address.address,//详细地址
                phone: that._address.phone,//联系电话
                first: that._address.first//默认 1是2否
              }, function (data, success) {
                if (success) {
                  that.$vux.toast.show({text: '添加成功!', width: '18em'});
                  that.$emit('child-update', that.address);//调用父组件方法更新地址列表
                }
                else {
                  that.$vux.toast.show({text: data,type:'text', width: '18em'});
                }
              })
            } else {
              that.$vux.toast.show({text: '当前登录信息过期!', width: '18em'});
              that.$router.go({path: '/login'});
            }
          }
        }
        else {
          that.$vux.toast.show({text: '请填写地址信息!',type:'warn', width: '18em'});
        }
      }
    }
  }
</script>

<style rel="stylesheet/less" lang="less">
  @import "../../assets/css/newaddress.less";
</style>



