<template>
  <div class="regiter">
    <div class="r_header">
      <x-header :left-options="{showBack:true,backText:''}">注册</x-header>
    </div>
    <div class="r_content">
      <!--手机号-->
      <!--未输入值时button禁止点击，边框字体颜色为#99d9fa,获取焦点后颜色为#009ff2-->
      <div class="cell_phone">
        <group>
          <x-input title="手机号"  placeholder="请输入手机号" :show-clear="true"></x-input>
        </group>
        <div class="r_btn">
          <x-button type="primary" plain @click.native="showPosition (position)">下一步</x-button>
          <div class="a_msg_3">
            <checker :max="1" default-item-class="default_class" selected-item-class="selected_class">
              <checker-item value="1" >
                <i class="opt_img"></i>
                <span class="opt_msg">

									已阅读并同意<i>《世峰户外商城用户注册协议》</i>
								</span>
              </checker-item>
            </checker>
          </div>
        </div>
      </div>
    </div>
    <toast v-model="showPositionValue" type="text" :time="2000" is-show-mask :text="text" :position="position" ></toast>
  </div>
</template>
<script>
  import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

  export default {
    components: {
      Checker,
      CheckerItem,
      XHeader,
      XButton,
      XInput,
      Toast,
      Group,
      Cell
    },
    data () {
      return {
        position: 'middle',
        showPositionValue: false,
        text:"手机号码输入有误 !",
      }
    },
    methods: {
      showPosition (position) {
        this.position = position;
        this.showPositionValue = true;
      }
    }
  }
</script>

<style rel="stylesheet/less" lang="less">
  @import "../../assets/css/regiter.less";
  i{
    font-style: normal;
  }
</style>



